import React from "react";
import { Link } from "react-router-dom";
import s from "./Navbar.css";

class Navbar extends React.Component {
  render() {
    return (
      <nav className={s.nav}>
        <ul>
          <li>
            <Link to="/">Home</Link>
          </li>
          <li>
            <Link to="/socket">Socket.io</Link>
          </li>
        </ul>
      </nav>
    );
  }
}

export default Navbar;



