import React, { Component } from "react";
import s from "./Messages.css";

class Messages extends Component {
  constructor() {
    super();
    this.state = {
      messages: [],
      currentMessage: "",
      username: "",
      signedIn: false
    };
    this.renderMessages = this.renderMessages.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleUserForm = this.handleUserForm.bind(this);
  }

  componentWillMount() {
    const { socket } = this.props;
    const { messages } = this.state;
    socket.on("chat message", msg => {
      messages.push(msg);
      this.setState({
        messages
      });
    });
    socket.on("add user", user => {
      console.log(`${user} just joined chat`);
    });
  }

  handleSubmit(e) {
    e.preventDefault();
    const { socket } = this.props;
    const { currentMessage, username } = this.state;
    socket.emit("chat message", {
      message: currentMessage,
      username: username
    });
    this.setState({
      currentMessage: ""
    });
  }

  handleChange(e) {
    const { value, name } = e.target;
    this.setState({
      [name]: value
    });
  }

  renderMessages() {
    const { messages } = this.state;
    if (messages.length > 0) {
      return messages.map((msg, i) => (
        <p className={`${i % 2 === 0 ? s.even : s.odd}`} key={msg + i}>
          {msg.username}: {msg.message}
        </p>
      ));
    } else {
      return <p>There is no current messages!</p>;
    }
  }

  handleUserForm(e) {
    e.preventDefault();
    this.props.socket.emit("add user", this.state.username);
    this.setState({
      signedIn: true
    });
  }

  renderView() {
    if (!this.state.signedIn) {
      return (
        <form className={s.userform} onSubmit={this.handleUserForm}>
          <div className={s.container}>
            <h3>Username:</h3>
            <input
              onChange={this.handleChange}
              name="username"
              value={this.state.username}
              type="text"
              className={`${s.userinput}`}
            />
            <button className={`${s.userbtn}`}>Add Name</button>
          </div>
        </form>
      );
    } else {
      return (
        <div>
          {this.renderMessages()}
          <form className={s.form} onSubmit={this.handleSubmit}>
            <input
              onChange={this.handleChange}
              name="currentMessage"
              value={this.state.currentMessage}
              type="text"
              className={s.input}
            />
            <button className={`${s.btn}`}>Send</button>
          </form>
        </div>
      );
    }
  }

  render() {
    const { messages } = this.state;
    return (
      <div className={s.messages}>
        <h1>Messaging Application</h1>
        {this.renderView()}
      </div>
    );
  }
}

export default Messages;



